body {
  max-width: 100%;
  color: var(--secondary-color--dark);
  background-repeat: no-repeat;
  font-family: eb-garamod, serif;
  font-style: normal;
  font-weight: 400;
  position: relative;
}

#cookies-eu-banner {
  z-index: 8000000;
  color: var(--secondary-color--dark);
  background-color: var(--blue-background);
  opacity: .9;
  width: 20em;
  text-align: left;
  border: 2px solid var(--yellow-border);
  margin-left: 3rem;
  font-family: inherit;
  position: fixed;
  bottom: 2em;
  left: -5px;
}

#cookies-eu-banner[style*="display: block"] {
  animation: 1s ease-in-out 1s forwards showCookieBanner;
}

#cookies-eu-banner.before-remove {
  animation: 1s ease-in-out forwards hideCookieBanner;
}

.cookies-container {
  text-align: center;
  letter-spacing: .1rem;
  flex-direction: column;
  padding: 1rem;
  font-size: 1.2rem;
  display: flex;
}

@keyframes showCookieBanner {
  from {
    transform: translateY(100%);
  }

  to {
    transform: translateY(0%);
  }
}

@keyframes hideCookieBanner {
  from {
    transform: translateY(0%);
  }

  to {
    transform: translateY(100%);
  }
}

.cookie--link {
  color: var(--secondary-color--dark);
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: 1rem;
  text-decoration: none;
}

.cookie--link:hover {
  text-decoration: underline;
}

.btn-cookies {
  color: var(--secondary-color--dark);
  cursor: pointer;
  margin-top: 1rem;
  padding: .25em .5em .2em;
  font-size: 1rem;
  transition: all .3s;
}

#cookies-eu-reject {
  border: 1px solid var(--secondary-color--dark);
  background: none;
  margin-top: .5rem;
}

#cookies-eu-reject:hover {
  background-image: linear-gradient(60deg, #5f86f2, #a65ff2, #f25fd0, #f25f61, #f2cb5f, #abf25f, #5ff281, #5ff2f0);
  -webkit-background-clip: text;
  color: #0000;
  transform: translateY(-2px);
}

#cookies-eu-accept {
  border: 1px solid var(--secondary-color--dark);
  background: none;
}

#cookies-eu-accept:hover {
  background-image: linear-gradient(60deg, #5f86f2, #a65ff2, #f25fd0, #f25f61, #f2cb5f, #abf25f, #5ff281, #5ff2f0);
  -webkit-background-clip: text;
  color: #0000;
  transform: translateY(-2px);
}

