  --primary-color: #f25f61;
  --secondary-color: #b6602f;
  --secondary-color--light: #62b8ff;
  --secondary-color--night: #81b4de;
  --secondary-color--dark: #1b1007;
  --tertiary-color: #fcfaf7;
  --tertiary-color--dark: #ece5db;
  --white: #fff;
  --blue-background: #c2e9f6;
  --blue-border: #72cce3;
  --blue-color: #96dcee;
  --yellow-background: #fffaa8;
  --yellow-border: #f5eb71;
  --indigo-background: #808fc7;
  --indigo-border: #5d6baa;
  --indigo-color: #6b7abb;
